.App-RegistryList.style-calloutBox {
    margin-bottom: 25px;
    height: 215px;
}

.App-RegistryList a {
    display: block;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #000000;
}

.App-RegistryList img {
    max-width: 100%;
    max-height: 193px;
}


